import { MoSerializationPlugin, MoServerSerializationPlugin } from '@ali/4ever-cangjie';
import jsonMLToValue from './jsonMLToValue';
import valueToJsonML from './valueToJsonML';
import htmlToJsonML from './htmlToJsonML';
import markdownToHTML from './markdownToHTML';
import htmlToMarkdown from './htmlToMarkdown';

export default function createSerializer(): MoSerializationPlugin {
  // inlineCode灰度过程中：
  // 关闭所有新InlineCode入口:编辑、粘贴html、粘贴markdown
  // 但无论新旧，均需具有新InlineCode模型的解析能力:故保留jsonMLToValue&valueToJsonML
  // 关闭新inlineCode的粘贴html入口:去除htmlToJsonML
  // 保留markdownToHTML：新旧InlineCode的markdown解析格式是一致的，复用即可
  return {
    jsonMLToValue,
    valueToJsonML,
    //TODO inline-code全量后需要将该入口打开
    // htmlToJsonML,
    markdownToHTML,
    htmlToMarkdown,
  };
}

export function createServerSerializationPlugin(): MoServerSerializationPlugin {
  return {
    valueToJsonML,
    jsonMLToValue,
  };
}
